import Vue from 'vue'
import Router from 'vue-router'
import innerHTML from '@/pages/buyerCenter/innerHTML/innerHTML';
import accountAll from '@/pages/buyerCenter/innerHTML/accountAll';
import agentBasic from '@/pages/buyerCenter/innerHTML/agentBasic';
import agentList from '@/pages/buyerCenter/innerHTML/agentList';
import autoReply from '@/pages/buyerCenter/innerHTML/autoReply';
import channelConfig from '@/pages/buyerCenter/innerHTML/channelConfig';
import material from '@/pages/buyerCenter/innerHTML/material';
import menuCustom from '@/pages/buyerCenter/innerHTML/menuCustom';
import publishManager from '@/pages/buyerCenter/innerHTML/publishManager';
import returndetail from '@/pages/buyerCenter/innerHTML/returndetail';
import todayTarget from '@/pages/buyerCenter/innerHTML/todayTarget';
import totalManager from '@/pages/buyerCenter/innerHTML/totalManager';
import recruitMonthReport from '@/pages/buyerCenter/innerHTML/recruitMonthReport';

import experienceProduct from '@/pages/buyerCenter/experienceProduct/experienceProduct';
import productView from '@/pages/buyerCenter/experienceProduct/productView';




Vue.use(Router)

export default new Router({
    routes: [
        // {
        //     path: '/',
        //     name: 'innerHTML',
        //     component: innerHTML,
        //     meta: { title: '' }
        // },
        // {
        //     path: '/innerHTML',
        //     name: 'innerHTML',
        //     component: innerHTML,
        //     meta: { title: '' }
        // },
        {
            path: '/',
            name: 'agentBasic',
            component: agentBasic,
            meta: { title: '' }
        },
        {
            path: '/todayTarget',
            name: 'todayTarget',
            component: todayTarget,
            meta: { title: '' }
        },
        {
            path: '/accountAll',
            name: 'accountAll',
            component: accountAll,
            meta: { title: '' }
        },  
        {
            path: '/agentBasic',
            name: 'agentBasic',
            component: agentBasic,
            meta: { title: '' }
        },  
        {
            path: '/agentList',
            name: 'agentList',
            component: agentList,
            meta: { title: '' }
        },  
        {
            path: '/autoReply',
            name: 'autoReply',
            component: autoReply,
            meta: { title: '' }
        },  
        {
            path: '/channelConfig',
            name: 'channelConfig',
            component: channelConfig,
            meta: { title: '' }
        },  
        {
            path: '/material',
            name: 'material',
            component: material,
            meta: { title: '' }
        },  
        {
            path: '/menuCustom',
            name: 'menuCustom',
            component: menuCustom,
            meta: { title: '' }
        },  
        {
            path: '/publishManager',
            name: 'publishManager',
            component: publishManager,
            meta: { title: '' }
        },  
        {
            path: '/returndetail',
            name: 'returndetail',
            component: returndetail,
            meta: { title: '' }
        },  
        {
            path: '/totalManager',
            name: 'totalManager',
            component: totalManager,
            meta: { title: '' }
        },           
        {
            path: '/recruitMonthReport',
            name: 'recruitMonthReport',
            component: recruitMonthReport,
            meta: { title: '' }
        },  
        {
            path: '/experienceProduct',
            name: 'experienceProduct',
            component: experienceProduct,
            meta: { title: '' }
        }, 
        {
            path: '/experienceProduct/productView',
            name: 'productView',
            component: productView,
            meta: { title: '' }
        },                                                       
    ]
})



